import Vue from 'vue'
import Router from 'vue-router'
import Hello from '../components/Hello'
import SearchCity from '../components/search'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Hello',
    component: Hello
  },{
    path: '/searchcity',
    component: SearchCity
  }]
})


